.app {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.grid {
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fill);
    grid-template-areas: "featured";
}

@media (min-width: 480px) {
    .grid {
        grid-template-areas: "featured featured"
    }
}

@media (min-width: 640px) {
    .grid {
        grid-template-areas: "featured featured featured"
    }
}

.grid__col:nth-child(2) {
    grid-area: featured;
}

.image {
    display: block;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}

.footer {
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 40px;
    color: #666;
    background-color: #222;
    font-size: 0.8rem;
}



